import Promise from 'bluebird';
import Hp from './_base';
import { format, DOM, log } from '../../../utils';
import bus from '../../bus';

const { toInt, toFloat } = format;
const { extractElement, extractFormValues, extractData } = DOM;

/** Behavior description:
 * - existing code: yes
 * - remove code: yes
 * - stackable: yes
 * - free gift support: yes
 * - 'isValid' implemented: yes
 * - manually apply code: no
 * - valid code can be replaced with another valid code:can't check, currently there is only one code
 * - site works very slow, so we can test only one code
 * - can run code testing only on Cart page
 * */

const BASE_URL = 'https://shop.usa.canon.com/shop';

const Driver = class Driver extends Hp {
	constructor() {
		super();
		this.BASE_URL = BASE_URL;
		this.APPLY_FORM_SELECTOR = '#PromotionCodeForm';
		this.TOTAL_SELECTOR = '#WC_SingleShipmentOrderTotalsSummary_td_13';
		this.IS_STACKABLE = true;
		this.BASKET_COUNT_SELECTOR = '#minishopcart_total';
		this.APPLIED_CODES_SELECTOR = '[id^=promotion_]';
		this.APPLIED_CODES_REGEXP = /"PromotionCodeForm","([^"]+)"/;
		this.ITEM_QUANTITY_REGEXP = /item_qtyInShopCart_/g;

		this.STORE_ID_SELECTOR = 'input[name="storeId"]';
		this.CATALOG_ID_SELECTOR = 'input[name="catalogId"]';
		this.LANG_ID_SELECTOR = 'input[name="langId"]';

		this.SHIPPING_SELECTOR = '#WC_SingleShipmentOrderTotalsSummary_td_41';
	}

	beforeTestCodes() {
		this.APPLY_FORM = extractFormValues(this.APPLY_FORM_SELECTOR);

		this.MINI_SHOP_REQUEST_BODY_PARAMS = new URLSearchParams();
		this.MINI_SHOP_REQUEST_BODY_PARAMS.set('deleteCartCookie', true);
		this.MINI_SHOP_REQUEST_BODY_PARAMS.set('objectId', '');
		this.MINI_SHOP_REQUEST_BODY_PARAMS.set('requesttype', 'ajax');

		this.MINI_SHOP_REQUEST_PARAMS = new URLSearchParams();
		this.MINI_SHOP_REQUEST_PARAMS.set('storeId', document.querySelector(this.STORE_ID_SELECTOR).value);
		this.MINI_SHOP_REQUEST_PARAMS.set('catalogId', document.querySelector(this.CATALOG_ID_SELECTOR).value);
		this.MINI_SHOP_REQUEST_PARAMS.set('langId', document.querySelector(this.LANG_ID_SELECTOR).value);

		return Promise.resolve();
	}

	getExistingCodes() {
		const codes = [...document.querySelectorAll(this.APPLIED_CODES_SELECTOR)].map(
			element => element.getAttribute('onClick').match(this.APPLIED_CODES_REGEXP)[1],
		);
		return {
			codes,
			codeCount: codes.length,
		};
	}

	removeCodes(codes) {
		return Promise.each(codes, code => this._removeCode(code))
			.then(() => this.getUpdatedCart())
			.then(html => ({
				originalPrice: this.parseTotal(html),
				originalItemsCount: this._parseItemsCount(html),
			}));
	}

	checkCodes(codes, originalPrice = this.parseTotal(), originalItemsCount = this._parseItemsCount()) {
		log(`Original price: ${originalPrice}`);
		log(`Original items count: ${originalItemsCount}`);
		const workingDiscounts = [];
		return Promise.each(codes, code =>
			this._applyCode(code).then(responseObject => {
				// no need to get updated cart or remove code if apply returned error
				if (responseObject.errorCode) {
					return bus.$emit('code-checked', {
						code,
						finalPrice: originalPrice,
						finalDiscount: 0,
						isValid: false,
					});
				}
				return this.getUpdatedCart().then(html => {
					const finalPrice = this.parseTotal(html);
					const finalDiscount = originalPrice - finalPrice;
					const freeGifts = this._parseItemsCount(html) - originalItemsCount;
					const result = { code, finalPrice, finalDiscount, freeGifts };
					if (
						finalPrice < originalPrice ||
						(result.finalPrice === originalPrice && Boolean(result.freeGifts))
					) {
						workingDiscounts.push(result);
					}
					return this._removeCode(code).then(() => bus.$emit('code-checked', result));
				});
			}),
		).then(() =>
			bus.$emit('stack-codes', {
				codes: workingDiscounts.map(discount => discount.code),
				discounts: workingDiscounts,
				originalPrice,
				originalItemsCount,
			}),
		);
	}

	stackCodes(validationInfo) {
		const { discounts, originalPrice, originalItemsCount } = validationInfo;

		discounts.sort((a, b) => b.finalDiscount - a.finalDiscount);

		let currentPrice = originalPrice;
		let currentDiscount = 0;
		let currentFreeGifts = 0;
		const successfulCodes = [];

		return Promise.each(discounts, discount =>
			this._applyCode(discount.code).then(responseObject => {
				const result = {
					code: discount.code,
					finalPrice: currentPrice,
					finalDiscount: currentDiscount,
				};
				// no need to get updated cart or remove code if apply returned error
				if (responseObject.errorCode) {
					return bus.$emit('code-stacked', result);
				}
				return this.getUpdatedCart().then(html => {
					result.finalPrice = this.parseTotal(html);
					result.finalDiscount = originalPrice - result.finalPrice;
					result.freeGifts = this._parseItemsCount(html) - originalItemsCount;

					bus.$emit('code-stacked', result);

					if (
						result.finalPrice < currentPrice ||
						(result.finalPrice === currentPrice && result.freeGifts > currentFreeGifts)
					) {
						successfulCodes.push(discount.code);
						currentDiscount = result.finalDiscount;
						currentPrice = result.finalPrice;
						currentFreeGifts = result.freeGifts;
						return Promise.resolve();
					}
					return this._removeCode(discount.code);
				});
			}),
		).then(() =>
			bus.$emit('stackable-complete', {
				codes: successfulCodes,
				finalPrice: currentPrice,
				finalDiscount: currentDiscount,
			}),
		);
	}

	makeApprovalRequest() {
		return super.makeApprovalRequest().then(() => this._deleteCartCookie());
	}

	getPreTaxShippingTotal() {
		return this._getUpdatedCart().then(responseHTML => {
			const total = this.parseTotal(responseHTML);
			const shipping = extractData(this.SHIPPING_SELECTOR, responseHTML);
			const calculatedShipping = shipping.toLowerCase().includes('free') ? 0 : shipping;
			return total - toFloat(calculatedShipping);
		});
	}

	_getUpdatedCart() {
		return fetch(window.location.href, {
			method: 'GET',
			credentials: 'include',
		}).then(response => response.text());
	}

	_deleteCartCookie() {
		return fetch(`${this.BASE_URL}/MiniShopCartDisplayView?${this.MINI_SHOP_REQUEST_PARAMS.toString()}`, {
			method: 'POST',
			credentials: 'include',
			body: this.MINI_SHOP_REQUEST_BODY_PARAMS,
			headers: {
				'x-requested-with': 'XMLHttpRequest',
				'content-type': 'application/x-www-form-urlencoded',
			},
		});
	}

	_parseItemsCount(data) {
		if (data) {
			// need to calculate count of items in a such way, because there is code,
			// which gives two the same gifts and they are placed in one row
			return [...data.match(this.ITEM_QUANTITY_REGEXP)].reduce(
				(count, el, i) => count + toInt(extractElement(`#${el}${i + 1}`, data).value),
				0,
			);
		}
		return toInt(extractElement(this.BASKET_COUNT_SELECTOR).textContent);
	}
};

export default {
	name: 'Canon',
	domain: 'canon.com',
	stackable: true,
	Driver,
	maxCoupons: 1, // site works very slow
	isOnCartUrl(href) {
		const hrefToLowerCase = href.toLowerCase();
		return (
			(hrefToLowerCase.startsWith(`${BASE_URL}/orderitemdisplay`) ||
				hrefToLowerCase.startsWith(`${BASE_URL}/ajaxcheckoutdisplayview`) ||
				hrefToLowerCase.startsWith(`${BASE_URL}/ajaxorderitemdisplayview`)) &&
			document.querySelector('#promoCode') &&
			document.querySelector('#order_total')
		);
	},
	isCartEmpty() {
		return Boolean(document.querySelector('#WC_EmptyShopCartDisplayf_div_1'));
	},
};
