import Promise from 'bluebird';
import Hp from './_base';
import bus from '../../bus';
import { handleDriverError } from '../../lib';
import { format, DOM, log } from '../../../utils';

const { toFloat, elemToFloat } = format;
const { extractElement, extractElementFromHTML } = DOM;

/*
Behavior description:
+ Existing code: yes
+ Remove code: yes
+ Stackable: no
+ Free gift support: no
+ Free shipping: no
+ ‘isValid’ implemented: yes
+ Codes can be replaced by another code: no
+ Manually apply code: no
+ Pages, where extension works: Cart
+ Is driver SPA: yes
+ maxCoupons: 12 (testing time)
+ Gather codes on site pages: no
=====Special case=====
+ We can't support checkout because of incorrect response from merchant, strange behavior on it
*/

const BASE_URL = 'https://store.hp.com';

const Driver = class Driver extends Hp {
	constructor() {
		super();
		this.BASE_URL = `${BASE_URL}/us/en`;
		this.TOTAL_ELEMENT_CONFIG = { selector: '#cartTotal' };
		this.APPLY_FORM_SELECTOR = '#searchHP';
		this.APPLIED_CODES_SELECTOR = '.removeCpn';
		this.ORDER_ID_SELECTOR = '#orderId';
		this.LANG_ID_SELECTOR = '[name="langId"]';
		this.STORE_ID_SELECTOR = '[name="storeId"]';
		this.CATALOG_ID_SELECTOR = '[name="catalogId"]';

		this.LOADING_ELEMENT_SELECTOR = '#loadingOverlay';
		this.INPUT_CODE_SELECTOR_ON_CART = '#cpnCode';

		this.IS_LOYALTY_SELECTOR = '#isLoyaltyProd';
		this.SHIPPING_SELECTOR = '[name="shipping-option"]:checked';
		this.SHIPPING_REGEX = /\((.*)\)/;
	}

	checkCondition() {
		const onCartEl = document.querySelector(this.INPUT_CODE_SELECTOR_ON_CART);
		const onCartCond = onCartEl ? onCartEl.offsetParent : false;
		const loadingEl = document.querySelector(this.LOADING_ELEMENT_SELECTOR);
		const loadingCond = loadingEl ? loadingEl.classList.contains('hide') : false;
		return !!onCartCond && loadingCond;
	}

	beforeTestCodes() {
		this.BODY_DATA = {};
		this.BODY_DATA.orderId = document.querySelector(this.ORDER_ID_SELECTOR).getAttribute('value');
		this.BODY_DATA.langId = document.querySelector(this.LANG_ID_SELECTOR).getAttribute('value');
		this.BODY_DATA.storeId = document.querySelector(this.STORE_ID_SELECTOR).getAttribute('value');
		this.BODY_DATA.catalogId = document.querySelector(this.CATALOG_ID_SELECTOR).getAttribute('value');

		this.APPLY_FORM = { ...this.BODY_DATA };
		this.REMOVE_FORM = { ...this.BODY_DATA };
		this.APPROVAL_REQUEST_PARAMS = { ...this.BODY_DATA };
		this.APPROVAL_REQUEST_PARAMS = new URLSearchParams(this.APPROVAL_REQUEST_PARAMS);
		this.APPROVAL_REQUEST_PARAMS.set('calculationUsage', '-1,-2,-5,-6,-7');

		const isLoyaltyEl = document.querySelector(this.IS_LOYALTY_SELECTOR).value;
		this.APPLY_FORM.isLoyaltyEligible = isLoyaltyEl || 'false';
		this.APPLY_FORM.finalView = 'AjaxOrderItemDisplayView';
		this.APPLY_FORM.loyaltyAmt = '0';
	}

	getExistingCodes() {
		const codes = [...document.querySelectorAll(this.APPLIED_CODES_SELECTOR)].map(element => element.dataset.cpn);
		return {
			codes,
			codeCount: codes.length,
		};
	}

	beforeCheckCodes() {
		// Sometimes driver gets bad original price from page
		return this.getUpdatedCart().then(html => this.parseTotal(html));
	}

	checkCodes(codes, originalPrice = this.parseTotal()) {
		log(`Original price: ${originalPrice}`);

		return Promise.each(codes, code =>
			this._applyCode(code).then(responseObject => {
				const result = {
					code,
					finalPrice: originalPrice,
					finalDiscount: 0,
					isValid: true,
				};
				if (!responseObject || responseObject.errorCode) {
					result.isValid = false;
					return bus.$emit('code-checked', result);
				}
				return this.getUpdatedCart().then(html => {
					result.finalPrice = this.parseTotal(html);
					result.finalDiscount = originalPrice - result.finalPrice;
					return this._removeCode(code).then(() => bus.$emit('code-checked', result));
				});
			}),
		);
	}

	applyCodes(codes) {
		return this._applyCode(codes[0]);
	}

	getPreTaxShippingTotal() {
		return this.getUpdatedCart().then(responseHTML => {
			const shippingEl = extractElement(this.SHIPPING_SELECTOR, responseHTML);
			const shipping = shippingEl
				? toFloat(shippingEl.nextElementSibling.textContent.match(this.SHIPPING_REGEX)[1]) || 0
				: 0;
			return this.parseTotal(responseHTML) - shipping;
		});
	}

	_manageCode(url, body) {
		return (
			fetch(url, {
				method: 'POST',
				credentials: 'include',
				headers: {
					'x-requested-with': 'XMLHttpRequest',
					'content-type': 'application/x-www-form-urlencoded; charset=UTF-8',
				},
				body,
			})
				// This request is required, otherwise coupon code won't be applied or removed
				.then(response => response.text())
				.then(responseText => {
					const responseObject = this._parseResponse(responseText);
					// if there is an error, there is no need to make approval request
					if (responseObject && !responseObject.errorCode) {
						return this.makeApprovalRequest().then(() => responseObject);
					}
					return responseObject;
				})
		);
	}

	_parseResponse(responseText) {
		try {
			return JSON.parse(
				responseText
					.replace('/*', '')
					.replace('*/', '')
					.trim(),
			);
		} catch (e) {
			if (responseText.match(/Access Denied/i)) {
				return null;
			}
			handleDriverError(new Error(`Invalid json string.`), { responseText });
			return null;
		}
	}

	_applyCode(code) {
		this.APPLY_FORM = new URLSearchParams(this.APPLY_FORM);
		this.APPLY_FORM.set('promoCode', code);
		this.APPLY_FORM.set('taskType', 'A');
		this.APPROVAL_REQUEST_PARAMS.set('tasktype[]', 'A');
		this.APPROVAL_REQUEST_PARAMS.set('promoCode', code);
		return this._manageCode(this._getUrl(), this.APPLY_FORM);
	}

	_removeCode(code) {
		this.REMOVE_FORM = new URLSearchParams(this.REMOVE_FORM);
		this.REMOVE_FORM.set('promoCode', code);
		this.REMOVE_FORM.set('taskType', 'R');
		this.APPROVAL_REQUEST_PARAMS.set('tasktype[]', 'R');
		this.APPROVAL_REQUEST_PARAMS.set('promoCode', code);
		return this._manageCode(this._getUrl(false), this.REMOVE_FORM);
	}

	parseTotal(html) {
		let totalElement = null;
		if (html) {
			totalElement = extractElementFromHTML(html, this.TOTAL_ELEMENT_CONFIG.selector);
		} else {
			totalElement = document.querySelector(this.TOTAL_ELEMENT_CONFIG.selector);
		}
		return elemToFloat(totalElement);
	}
};

export default {
	name: 'HP',
	domain: 'hp.com',
	Driver,
	maxCoupons: 12,
	isSinglePageApp: true,
};
