import Promise from 'bluebird';
import Hp from './_base';
import bus from '../../bus';
import { DOM, sendXhr, format, log } from '../../../utils';

const { extractFormValues, extractElement } = DOM;
const { elemToFloat } = format;

/* Behavior description:
	+ Existing code: yes
	+ Remove code: yes
	+ Stackable: no
	+ Free gift support: no
	+ Free shipping: no
	+ ‘isValid’ implemented: yes
	+ Codes can be replaced by another code: yes
	+ Manually apply code: no
	+ Is driver SPA: yes
	+ Gather codes on site pages: yes(cart page)
=======================
Special case:
	- Shipping request required on the checkout page, for right total
	- maxCoupons: 2, due to blocking
	- don't allow code testing when user has applied code
*/

const { href } = window.location;
const BASE_URL = 'https://www.petco.com';
const IS_CHECKOUT = href.includes('/SingleShipmentOrderSummaryView');

const Driver = class Driver extends Hp {
	constructor() {
		super();
		this.BASE_URL = `${BASE_URL}/shop`;
		this.TOTAL_SELECTOR =
			'#WC_SingetUpdatedCartgleShipmentOrderTotalsSummary_td_13, #WC_SingleShipmentOrderTotalsSummary_td_10, #WC_SingleShipmentOrderTotalsSummary_td_13';
		this.APPLY_FORM = extractFormValues('#PromotionCodeForm');
		this.APPLIED_CODES_SELECTOR = '.applied-promo a, #petcoPromotionDisplayArea .form-field-name';

		this.PROMO_SELECTOR = '#payment-information-promo-code, #promo-display';
		this.EMPTY_CRT_SELECTOR = '.empty-cart';

		this.NEWRELIC_ID_REGEX = /xpid:"([^"]*)"/;
		this.NEWRELIC_ID = null;

		this.CODE_INPUT_SELECTOR = '#enter-promo-code';
		this.APPLY_BUTTON_SELECTOR = '#WC_PromotionCodeDisplay_links_1';
		this.updateBodyData = new URLSearchParams({
			beginIndex: 0,
			fromPage: 'paymentMethodPage',
			objectId: '',
			requesttype: 'ajax',
		});
		this.shippingUpdateData = null;

		this.INDEX_SELECTOR = '.order-summary li span:first-child';
		this.VALUE_SELECTOR = '.order-summary li span:last-child';
		this.ORDER_ID_SELECTOR = '#orderIdForPurcList';
		this.TOTAL_PRICE_REGEXP = /orderTotal:\s([0-9.]+)/;
		this.MERCHANT_CODE_SELECTOR = '#promo-strip-carousel strong';
	}

	checkCondition() {
		const promoInput = document.querySelector(this.PROMO_SELECTOR);
		const isCartEmpty = document.querySelector(this.EMPTY_CRT_SELECTOR);
		const isOnRightUrl =
			(href.includes('/SingleShipmentOrderSummaryView') &&
				!document.querySelector('#payment-section.disabled-section')) ||
			href.includes('/OrderCheckoutView') ||
			href.includes('/OrderItemDisplay') ||
			href.includes('/OrderCalculate') ||
			href.includes('/AjaxCheckoutDisplayView');
		// don't allow code testing when user has applied code
		const isCodeApplied = document.querySelector(this.APPLIED_CODES_SELECTOR);
		return isOnRightUrl && promoInput && !isCodeApplied && !isCartEmpty;
	}

	beforeTestCodes() {
		this.APPROVAL_REQUEST_PARAMS = new URLSearchParams({
			storeId: 10151,
			catalogId: 10051,
			langId: -1,
			orderId: '.',
			calculationUsage: '-1,-3,-4,-6,-7',
			requesttype: 'ajax',
		});
		[, this.NEWRELIC_ID] = [...document.querySelectorAll('script')]
			.find(element => element.text.match(this.NEWRELIC_ID_REGEX))
			.text.match(this.NEWRELIC_ID_REGEX);
		if (IS_CHECKOUT) {
			this.shippingUpdateData = new URLSearchParams({
				storeId: 10151,
				catalogId: 10051,
				langId: -1,
				orderId: document.querySelector(this.ORDER_ID_SELECTOR).value,
				requesttype: 'ajax',
			});
		}
		return Promise.resolve();
	}

	getMerchantCodes() {
		return this.getUpdatedCart().then(resHtml => {
			return [
				...new Set(
					[...extractElement(this.MERCHANT_CODE_SELECTOR, resHtml, true)]
						.map(item => item.textContent)
						.filter(Boolean),
				),
			];
		});
	}

	getExistingCodes() {
		const codes = [...document.querySelectorAll(this.APPLIED_CODES_SELECTOR)].map(el => el.textContent);

		return {
			codes,
			codeCount: codes.length,
		};
	}

	checkCodes(codes, originalPrice = this.parseTotal()) {
		log(`Original price: ${originalPrice}`);
		return Promise.each(codes, code =>
			this._applyCode(code).then(responseObject => {
				// no need to get updated cart or remove code if apply returned error
				if (responseObject.errorCode) {
					return bus.$emit('code-checked', {
						code,
						finalPrice: originalPrice,
						finalDiscount: 0,
						isValid: false,
					});
				}
				return this.getUpdatedData().then(html => {
					const finalPrice = this.parseTotal(html);
					const finalDiscount = originalPrice - finalPrice;
					const result = { code, finalPrice, finalDiscount };
					if (finalPrice < originalPrice) {
						result.isValid = true;
					}
					return this._removeCode(code).then(() => bus.$emit('code-checked', result));
				});
			}),
		);
	}

	_parseResponse(responseText) {
		if (responseText.includes('permission')) {
			return { errorCode: 'error' };
		}
		return JSON.parse(
			responseText
				.replace('/*', '')
				.replace('*/', '')
				.trim(),
		);
	}

	_manageCode(code, url) {
		const body = this._getManageCodeParams(code);
		return (
			sendXhr(url, {
				method: 'POST',
				credentials: 'include',
				headers: {
					'x-requested-with': 'XMLHttpRequest',
					'Content-Type': 'application/x-www-form-urlencoded',
					'x-newrelic-id': this.NEWRELIC_ID,
				},
				body,
			})
				// This request is required, otherwise coupon code won't be applied or removed
				.then(response => response.text())
				.then(responseText => {
					const responseObject = this._parseResponse(responseText);
					// if there will be error, there is no need to make approval request
					if (!responseObject.errorCode) {
						this.makeApprovalRequest().then(() => responseObject);
					}
					return responseObject;
				})
		);
	}

	makeApprovalRequest() {
		return sendXhr(`${this.BASE_URL}/AjaxOrderChangeServiceItemUpdate`, {
			method: 'POST',
			credentials: 'include',
			headers: {
				'x-requested-with': 'XMLHttpRequest',
				'Content-Type': 'application/x-www-form-urlencoded',
				'x-newrelic-id': this.NEWRELIC_ID,
			},
			body: this.APPROVAL_REQUEST_PARAMS,
		});
	}

	getUpdatedData() {
		if (IS_CHECKOUT) {
			this.regExpTotal = true;
			return sendXhr(`${this.BASE_URL}/orderTotalAsJSON`, {
				method: 'POST',
				credentials: 'include',
				headers: {
					'x-requested-with': 'XMLHttpRequest',
					'Content-Type': 'application/x-www-form-urlencoded',
					'x-newrelic-id': this.NEWRELIC_ID,
				},
				body: new URLSearchParams({ storeId: 10151, catalogId: 10051, langId: -1 }),
			}).then(response => response.text());
		}
		return this.getUpdatedCart();
	}

	getUpdatedCart() {
		return sendXhr(window.location.href, {
			method: 'GET',
			headers: {
				'x-requested-with': 'XMLHttpRequest',
				'Content-Type': 'application/x-www-form-urlencoded',
				'x-newrelic-id': this.NEWRELIC_ID,
			},
			credentials: 'include',
		}).then(response => response.text());
	}

	getPreTaxShippingTotal() {
		return this.getUpdatedCart().then(responseHTML => {
			const getValue = regEx => {
				const index = [...extractElement(this.INDEX_SELECTOR, responseHTML, true)]
					.map(el => regEx.test(el.textContent))
					.indexOf(true);
				return index !== -1
					? elemToFloat([...extractElement(this.VALUE_SELECTOR, responseHTML, true)][index])
					: 0;
			};
			const totalValue = this.parseTotal(responseHTML);
			const taxValue = getValue(/taxes/i);
			const shippingValue = getValue(/shipping/i);
			const donation = getValue(/donation/i);

			return totalValue - taxValue - shippingValue - donation;
		});
	}

	parseTotal(data) {
		if (this.regExpTotal) {
			this.regExpTotal = false;

			return data.match(this.TOTAL_PRICE_REGEXP)[1];
		}
		return elemToFloat(extractElement(this.TOTAL_SELECTOR, data));
	}

	updatedShipping() {
		return sendXhr(`${this.BASE_URL}/PetcoAjaxShippingMethodUpdateCmd`, {
			method: 'POST',
			headers: {
				'x-requested-with': 'XMLHttpRequest',
				'Content-Type': 'application/x-www-form-urlencoded',
				'x-newrelic-id': this.NEWRELIC_ID,
			},
			credentials: 'include',
			body: this.shippingUpdateData,
		});
	}
};

export default {
	name: 'Petco',
	domain: 'petco.com',
	Driver,
	maxCoupons: 2, // due to blocking
	isSinglePageApp: true,
};
