import Hp from './_base';
import { DOM } from '../../../utils';

const { extractFormValues } = DOM;

/** Behavior description:
 * - can get existing codes
 * - can remove codes
 * - can apply multiple codes (stackable)
 * ======== Special case ========
 * extension runs about 35 sec with one valid code, so I decreased the number of maxCoupons to 5
 * */

const BASE_URL = 'https://estore.canon.ca';

const Driver = class Driver extends Hp {
	constructor() {
		super();
		this.BASE_URL = `${BASE_URL}/webapp/wcs/stores/servlet`;
		this.TOTAL_SELECTOR = '.table_orderTotal .row:last-of-type .total_figures';
		this.FORM_SELECTOR = '#PromotionCodeForm';
		this.APPLIED_CODES_REG = /"PromotionCodeForm","([^"]+)"/;
		this.IS_STACKABLE = true;
		this.APPLIED_CODE_SELECTOR = '[id^=promotion_]';
		this.APPLY_FORM = null;
		this.SPINNER_SELECTOR = '#progress_bar_dialog';
		this.CODE_INPUT_SELECTOR = '#promoCode';
	}

	checkCondition() {
		const spinner = document.querySelector(this.SPINNER_SELECTOR);
		const { href } = window.location;
		const isSpinner = spinner && spinner.offsetParent;
		const isPromoInput = document.querySelector(this.CODE_INPUT_SELECTOR);
		const currentState =
			!isSpinner &&
			isPromoInput &&
			(href.includes('AjaxOrderItemDisplayView') ||
				href.includes('OrderShippingBillingView') ||
				href.includes('AjaxCheckoutDisplayView'));
		return currentState;
	}

	beforeTestCodes() {
		this.APPLY_FORM = extractFormValues(this.FORM_SELECTOR);
		return Promise.resolve();
	}

	getExistingCodes() {
		const codes = [...document.querySelectorAll(this.APPLIED_CODE_SELECTOR)].map(
			element => element.getAttribute('onClick').match(this.APPLIED_CODES_REG)[1],
		);
		return {
			codes,
			codeCount: codes.length,
		};
	}
};

export default {
	name: 'Canon Canada',
	domain: 'estore.canon.ca',
	Driver,
	stackable: true,
	isSinglePageApp: true,
	maxCoupons: 5,
	isOnCartUrl: () => false,
};
