import Hp from './_base';
import { DOM } from '../../../utils';
import { elemToFloat } from '../../../utils/format';

const { extractFormValues, extractElement } = DOM;

/* Behavior description:
+ Existing code: yes
+ Remove code: yes
+ Stackable: yes
+ Free gift support: no
+ Free shipping: no
+ ‘isValid’ implemented: yes
+ Manually apply code: no
+ Pages, where extension works: Cart, Checkout(without Shipping Step)
+ Is driver SPA: yes
+ Gather codes on site pages: yes
 */

const BASE_URL = 'https://www.davidsbridal.com';
const isCheckoutPage = window.location.href.includes('/OrderCheckoutView');

const Driver = class Driver extends Hp {
	constructor() {
		super();
		this.BASE_URL = `${BASE_URL}/webapp/wcs/stores/servlet`;
		this.TOTAL_SELECTOR = `#WC_SingleShipmentOrderTotalsSummary_td_${isCheckoutPage ? '10' : '13'}`;
		this.TAX_SELECTOR = '#WC_SingleShipmentOrderTotalsSummary_td_6';
		this.SHIPPING_SELECTOR = '#WC_SingleShipmentOrderTotalsSummary_td_8';
		this.APPLY_FORM = null;
		this.APPLIED_CODES_SELECTOR = '.promotion_used div';
		this.CODE_INPUT_SELECTOR = '[name="promoCode"]';
		this.EMPTY_CART_SELECTOR = '[id^=WC_EmptyShopCartDisplay]';
		this.SHIPPING_FORM_SELECTOR = '#shippingAddressForm';
		this.APPLY_FORM_SELECTOR = '#PromotionCodeForm';
		this.PROPOSE_CODES_SELECTOR = '.offerBody b';
		this.PAGE_CODES_SELECTOR = '[id*=monetate_selectorBanner] img';
		this.PAGE_CODE_REGEX = /code (\w+)/i;
		this.MERCHANT_CODE_REGEX = /Use code: (\w+)/;
		this.IS_STACKABLE = true;
	}

	checkCondition() {
		const { href } = window.location;
		const isOnRightURL =
			href.includes('/AjaxOrderItemDisplayView') || href.startsWith(`${BASE_URL}/OrderCheckoutView`);
		const emptyCartEl = document.querySelector(this.EMPTY_CART_SELECTOR);
		const isOnShipping = document.querySelector(this.SHIPPING_FORM_SELECTOR);
		return isOnRightURL && !emptyCartEl && !isOnShipping;
	}

	beforeTestCodes() {
		this.APPLY_FORM = extractFormValues(this.APPLY_FORM_SELECTOR);
	}

	getMerchantCodes() {
		// can get merchant codes from page and request's response
		const pageCodes = [...document.querySelectorAll(this.PAGE_CODES_SELECTOR)]
			.map(el => el.getAttribute('alt'))
			.filter(Boolean)
			.map(el => el.match(this.PAGE_CODE_REGEX))
			.filter(Boolean)
			.map(el => el[1]);
		const pageCodesUnique = [...new Set(pageCodes)];

		return this._getProposedCoupons().then(responseHTML => {
			if (!responseHTML) {
				return pageCodesUnique;
			}
			const proposedCodes = [...extractElement(this.PROPOSE_CODES_SELECTOR, responseHTML, true)]
				.map(el => el.textContent.trim().match(this.MERCHANT_CODE_REGEX))
				.filter(Boolean)
				.map(el => el[1]);
			return [...pageCodesUnique, ...proposedCodes];
		});
	}

	getExistingCodes() {
		const codes = [...document.querySelectorAll(this.APPLIED_CODES_SELECTOR)].map(
			element => element.textContent.split(/\s/)[0],
		);
		return {
			codes,
			codeCount: codes.length,
		};
	}

	getPreTaxShippingTotal() {
		return this.getUpdatedCart().then(response => {
			const tax = elemToFloat(extractElement(this.TAX_SELECTOR, response)) || 0;
			const shipping = elemToFloat(extractElement(this.SHIPPING_SELECTOR, response)) || 0;
			return this.parseTotal(response) - tax - shipping;
		});
	}

	_getProposedCoupons() {
		return fetch(`${BASE_URL}/Content_BuyOnline_offers`, {
			method: 'GET',
			credentials: 'include',
		}).then(response => response.text());
	}
};

export default {
	name: 'David`s Bridal',
	domain: 'davidsbridal.com',
	Driver,
	stackable: true,
	isSinglePageApp: true,
};
