import Promise from 'bluebird';
import BaseDriver from '../base';
import bus from '../../bus';
import { format, DOM, log } from '../../../utils';

const { elemToFloat } = format;
const { extractElementFromHTML } = DOM;

export default class Driver extends BaseDriver {
	constructor() {
		super();
		// Initialize base url (will be used as base url of requests)
		this.BASE_URL = '';
		// Initialize selectors
		this.TOTAL_SELECTOR = '';
		this.CHECKOUT_TOTAL_SELECTOR = '';
		this.APPLIED_CODES_SELECTOR = '';
		// Initialize apply form (just use extractFormValues to rewrite with your data)
		this.APPLY_FORM = null;
		// Rewrite with true if your merchant has stackable driver
		this.IS_STACKABLE = false;
		// Rewrite with false if your merchant use separate urls for apply and remove
		this.APPLY_WITH_MANAGE_CODE_URL = true;

		// default params for approval request
		this.APPROVAL_REQUEST_PARAMS = new URLSearchParams();
		this.APPROVAL_REQUEST_PARAMS.set('orderId', '.');
		this.APPROVAL_REQUEST_PARAMS.set('calculationUsage', '-1,-2,-5,-6,-7');
		this.APPROVAL_REQUEST_PARAMS.set('requesttype', 'ajax');
		// Initialize order id
		const orderIDElement = document.querySelector('input[name="orderId"]');
		this.ORDER_ID = orderIDElement ? orderIDElement.value : null;
	}

	applyCodes(codes) {
		if (this.IS_STACKABLE) {
			return Promise.resolve();
		}
		return this._applyCode(codes[0]);
	}

	checkCodes(codes, originalPrice = this.parseTotal()) {
		log(`Original price: ${originalPrice}`);

		const workingDiscounts = [];
		return Promise.each(codes, code =>
			this._applyCode(code).then(responseObject => {
				// no need to get updated cart or remove code if apply returned error
				if (responseObject.errorCode) {
					return bus.$emit('code-checked', {
						code,
						finalPrice: originalPrice,
						finalDiscount: 0,
						isValid: false,
					});
				}
				return this.getUpdatedCart().then(html => {
					const finalPrice = this.parseTotal(html);
					const finalDiscount = originalPrice - finalPrice;
					const result = { code, finalPrice, finalDiscount, isValid: true };
					if (this.IS_STACKABLE && finalPrice < originalPrice) {
						workingDiscounts.push(result);
					}
					return this._removeCode(code).then(() => bus.$emit('code-checked', result));
				});
			}),
		).then(() => {
			if (this.IS_STACKABLE) {
				bus.$emit('stack-codes', {
					codes: workingDiscounts.map(discount => discount.code),
					discounts: workingDiscounts,
					originalPrice,
				});
			}
			return Promise.resolve();
		});
	}

	stackCodes(validationInfo) {
		const { discounts, originalPrice } = validationInfo;

		discounts.sort((a, b) => b.finalDiscount - a.finalDiscount);

		let currentPrice = originalPrice;
		let currentDiscount = 0;
		const successfulCodes = [];

		return Promise.each(discounts, discount =>
			this._applyCode(discount.code).then(responseObject => {
				const result = { code: discount.code, finalPrice: currentPrice, finalDiscount: currentDiscount };
				// apply failed, no need to remove anything or check the price
				if (responseObject.errorCode) {
					bus.$emit('code-stacked', result);
					return Promise.resolve();
				}
				return this.getUpdatedCart().then(html => {
					const finalPrice = this.parseTotal(html);
					const finalDiscount = originalPrice - finalPrice;
					result.finalPrice = finalPrice;
					result.finalDiscount = finalDiscount;

					bus.$emit('code-stacked', result);

					if (finalPrice < currentPrice) {
						successfulCodes.push(discount.code);
						currentDiscount = finalDiscount;
						currentPrice = finalPrice;
						return Promise.resolve();
					}
					return this._removeCode(discount.code);
				});
			}),
		).then(() => {
			bus.$emit('stackable-complete', {
				codes: successfulCodes,
				finalPrice: currentPrice,
				finalDiscount: currentDiscount,
			});
			return Promise.resolve();
		});
	}

	/**
	 * Get object from response (to see if there any errors)
	 *
	 * @param {String} responseText
	 * @returns {Object}
	 * @private
	 */
	_parseResponse(responseText) {
		return JSON.parse(
			responseText
				.replace('/*', '')
				.replace('*/', '')
				.trim(),
		);
	}

	removeCodes(codes) {
		return Promise.each(codes, code => this._removeCode(code))
			.then(() => this.getUpdatedCart())
			.then(html => this.parseTotal(html));
	}

	/**
	 * We are supporting both combined (manageCode) and separate (applyCode/removeCode) urls
	 * Method is used to get urls out of driver param
	 *
	 * @param {Boolean} isApply
	 * @returns {string}
	 * @private
	 */
	_getUrl(isApply = true) {
		if (this.APPLY_WITH_MANAGE_CODE_URL) {
			return `${this.BASE_URL}/AjaxPromotionCodeManage`;
		}
		return `${this.BASE_URL}/${isApply ? 'AjaxRESTPromotionCodeApply' : 'AjaxRESTPromotionCodeRemove'}`;
	}

	_applyCode(code) {
		this.APPLY_FORM.params.set('taskType', 'A');
		return this._manageCode(code, this._getUrl());
	}

	_removeCode(code) {
		this.APPLY_FORM.params.set('taskType', 'R');
		return this._manageCode(code, this._getUrl(false));
	}

	/**
	 * Get params for manage code request
	 *
	 * @param {String} code
	 * @private
	 */
	_getManageCodeParams(code) {
		// sometimes form does not contain this parameters, so we check if they are not present, we add them
		if (!this.APPLY_FORM.params.get('requesttype')) {
			this.APPLY_FORM.params.set('finalView', 'AjaxOrderItemDisplayView');
			this.APPLY_FORM.params.set('requesttype', 'ajax');
			this.APPLY_FORM.params.set('orderId', this.ORDER_ID);
		}
		this.APPLY_FORM.params.set('promoCode', code);
		return this.APPLY_FORM.params;
	}

	_manageCode(code, url) {
		const body = this._getManageCodeParams(code);
		return (
			fetch(url, {
				method: 'POST',
				credentials: 'include',
				headers: {
					'x-requested-with': 'XMLHttpRequest',
					'Content-Type': 'application/x-www-form-urlencoded',
				},
				body,
			})
				// This request is required, otherwise coupon code won't be applied or removed
				.then(response => response.text())
				.then(responseText => {
					const responseObject = this._parseResponse(responseText);
					// if there will be error, there is no need to make approval request
					if (!responseObject.errorCode) {
						return this.makeApprovalRequest().then(() => responseObject);
					}
					return responseObject;
				})
		);
	}

	makeApprovalRequest() {
		return fetch(`${this.BASE_URL}/AjaxOrderChangeServiceItemUpdate`, {
			method: 'POST',
			credentials: 'include',
			headers: {
				'x-requested-with': 'XMLHttpRequest',
				'Content-Type': 'application/x-www-form-urlencoded',
			},
			body: this.APPROVAL_REQUEST_PARAMS,
		});
	}

	getUpdatedCart() {
		return fetch(window.location.href, {
			method: 'GET',
			credentials: 'include',
		}).then(response => response.text());
	}

	parseTotal(html) {
		let totalElement = null;
		if (html) {
			totalElement = extractElementFromHTML(html, this.TOTAL_SELECTOR);
		} else {
			totalElement = document.querySelector(this.TOTAL_SELECTOR);
		}
		return elemToFloat(totalElement);
	}
}
